// src/index.ts
import { EventEmitter } from "eventemitter3";
import { client as client2, kiss } from "kiss-core";

// src/utils.ts
function randomId() {
  return Math.random().toString(16).slice(2, 6);
}
function genRespEventName(eventName, id) {
  return `Res_${eventName}#${id}`;
}
var utils = {
  randomId,
  genRespEventName
};

// src/cache.ts
import { client } from "kiss-core";
async function getConfig(cacheName = "cache_name", initConfig) {
  return await client.mg.clientStorage.getAsync(cacheName) ?? initConfig;
}
async function updateConfig(cacheName, newConfig) {
  await client.mg.clientStorage.setAsync(cacheName, newConfig);
}

// src/index.ts
var MyEvent = class extends EventEmitter {
  ifUI;
  ifMg;
  ifMgUi;
  constructor(ifRenderUI = false, ifMg = false, ifMgUi = false) {
    super();
    this.ifUI = ifRenderUI;
    this.ifMg = ifMg;
    this.ifMgUi = ifMgUi;
    this.init();
  }
  init() {
    const receive = (result) => {
      if (result && result.event) {
        this.emit(result.event, result.data);
      }
    };
    if (this.ifUI) {
      if (this.ifMgUi) {
        try {
          window.onmessage = (ev) => receive(ev.data);
        } catch (e) {
        }
      } else {
        window.onmessage = (ev) => receive(ev.data.pluginMessage);
      }
    } else {
      client2.ui.onmessage = (data) => {
        receive(data);
      };
    }
  }
  send(event, data = "") {
    if (typeof event !== "string")
      throw new Error("Expected first argument to be an event name string");
    const postData = {
      event,
      data
    };
    if (this.ifUI) {
      if (this.ifMgUi) {
        try {
          window.parent.postMessage(postData, "*");
        } catch (e) {
        }
      } else {
        parent.postMessage({ pluginMessage: postData }, "*");
      }
    } else {
      client2.ui.postMessage(postData);
    }
  }
  // type support
  answer(event, handler) {
    return super.on(event, handler);
  }
  query(event, data) {
    this.send(event, data);
  }
  //answer and back data by RespEventName to ui
  answerBack(event, handler) {
    return super.on(event, async (data) => {
      const id = data.id;
      const respEventName = utils.genRespEventName(event, id);
      const respData = handler(data);
      this.send(respEventName, respData);
    });
  }
  //query and back data by RespEventName to ui
  queryBack(event, data) {
    const id = utils.randomId();
    const respEventName = utils.genRespEventName(event, id);
    console.log("respEventName", respEventName);
    return new Promise((resolve, reject) => {
      super.once(respEventName, (data2) => {
        if (data2.success) {
          resolve(data2.data);
        } else {
          reject(data2.data);
        }
      });
      this.send(event, { data, id });
    });
  }
  // queryBack and set max timeout
  queryBackMaxWait(event, data, timeout = 3e3) {
    const id = utils.randomId();
    const respEventName = utils.genRespEventName(event, id);
    return new Promise((resolve, reject) => {
      super.once(respEventName, (data2) => {
        if (data2.success) {
          resolve(data2.data);
        } else {
          reject(data2.data);
        }
      });
      this.send(event, { data, id });
      setTimeout(() => {
        reject("timeout");
      }, timeout);
    });
  }
};
var io_ui = kiss.inUi ? new MyEvent(true, kiss.inMg, kiss.inMgUi) : void 0;
var io_hook = kiss.inUi ? void 0 : new MyEvent(false, kiss.inMg, kiss.inMgUi);
var tool = utils;
var cache = {
  getConfig,
  updateConfig
};
export {
  MyEvent,
  cache,
  io_hook,
  io_ui,
  tool
};
